import React from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.css";

import Home from "./components/Home";
import About from "./components/About";
import Preferences from "./components/Preferences";
import Recommendation from "./components/Recommendation";

export default () => (
  <Router>
    <Route exact path="/recommend-rumney" component={Home} />
    <Route path="/recommend-rumney/about" component={About} />
    <Route path="/recommend-rumney/preferences" component={Preferences} />
    <Route path="/recommend-rumney/recommendation" component={Recommendation} />
  </Router>
);
